@media screen and (max-width: 800px) {
  .my-approach-container .main-header {
    margin-left: 8px;
  }
  .my-approach-container .cards-container {
    flex-wrap: wrap;
  }
  .my-approach-container .cards-container .card {
    max-width: unset;
  }
  .my-approach-container .cards-container .card .card-border-container {
    flex-direction: row;
    height: 152px;
  }
  .my-approach-container .cards-container .card .card-border-container .card-header {
    width: 240px;
  }
  .my-approach-container .cards-container .card .card-border-container .card-content {
    width: 360px;
  }
  .my-approach-container .cards-container .card .card-4-header {
    max-width: 253px;
  }
  .my-approach-container .cards-container .card .ux {
    top: 125px;
    left: 3px;
    width: 124px;
    height: 72px;
  }
  .my-approach-container .cards-container .card .ui {
    top: 80px;
    left: 210px;
    width: 104px;
    height: 72px;
  }
  .my-approach-container .cards-container .card .heart {
    top: 110px;
    left: 0;
  }
  .my-approach-container .cards-container .card .plane {
    top: 40px;
    left: 92px;
    rotate: 45deg;
  }
  .my-approach-container .cards-container .card .new {
    top: 95px;
    left: 90px;
    rotate: -15deg;
  }
}

